function buttonShowHide() {
    $('header .main-header .header-bottom .button-toggle').on('click', function () {
        $('header .mobile-wrapper').addClass('active')
    })
    $('header .mobile-wrapper .heading .close-mobile-wrapper').on('click', function () {
        $('header .mobile-wrapper').removeClass('active')
    })
}

function mappingmenubottom() {
    return new MappingListener({
        selector: "header .list-item",
        mobileWrapper: "header .mobile-wrapper",
        mobileMethod: "appendTo",
        desktopWrapper: "header .menu",
        desktopMethod: "appendTo",
        breakpoint: 1025
    }).watch();
}

