function toggleMobileHeader() {
    $('header .toggle-button').on('click', function () {
        $(this).toggleClass('active')
        $('header .mobile-wrapper').toggleClass('active')
        $('header .backdrop-wrapper').toggleClass('active')
    })
    $('header .backdrop-wrapper').on('click', function () {
        $('header .toggle-button').removeClass('active')
        $('header .mobile-wrapper').removeClass('active')
        $('header .backdrop-wrapper').removeClass('active')
    })
}

function BlogDetailShare() {
    $("#share").jsSocials({
        showCount: false,
        showLabel: false,
        shares: [
            {
                share: 'facebook',
                logo: 'fab fa-facebook-square',
                shareUrl: "https://facebook.com/sharer/sharer.php?u={url}",
                countUrl: "http://graph.facebook.com/?id={url}",
                getCount: function (data) {
                    return data.share && data.share.share_count || 0;
                },
                titel: "Share with Facebook"
            },
        ]
    });
}

