function tabActive() {
    $(".dc-dichvu-ct .tab-news-ct ul li a").on("click", function () {
        $(this)
            .parents(".dc-dichvu-ct .tab-news-ct ul")
            .find("li")
            .removeClass("active");
        $(this)
            .parents("li")
            .addClass("active");

        var display = $(this).attr("data-tab");
        $(".tab-content").removeClass("active");
        $("#" + display).addClass("active");
    });
}

function slideToogleFormApply() {
    $('.dao-cham-td-ct .main-button .ung-tuyen a').on('click', function () {
        $('.dao-cham-td-ct .form-apply').slideToggle()
    })
}
function gotop() {
    $('#gotop').on('click', function() {
        $('html,body').animate({
            scrollTop: 0
        }, 1000)
    })
  }
function ButtonShowSearch(){
    $('header .main-header .header-top .button-search').on('click', function(){
        $('header .Module-281').addClass('active')
        $('header .backdrop').addClass('active')
    })
    $('header .backdrop').on('click', function () {
        $('header .Module-281').removeClass('active')
        $('header .backdrop').removeClass('active')
    })
}
function ShowBookTour(){
    $('.button-small .book').on('click', function(){
        $('.dc-tour-dulich-2 .main-form').addClass('active')
        $('.dc-tour-dulich-2 .backdrop').addClass('active')
    })
    $('.dc-tour-dulich-2 .backdrop').on('click', function () {
        $('.dc-tour-dulich-2 .main-form').removeClass('active')
        $('.dc-tour-dulich-2 .backdrop').removeClass('active')
    })
}
